.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



/* ESTILOS PAGINA INICIO */

.logo-h1{
  animation: App-logo-spin infinite 5s ease-in-out;
  margin-right: 0.5rem;
}

.main-container {
  background-color: #282c34;
  width: 100%;
  height: auto;
  color: white;
}

.main-container h1 {
  font-size: 2rem;
  margin: 0;
  padding: 2rem;
}

.search-form{
  display: flex;
  width: 30rem;
  margin: auto;
  background-color: white;
  border-radius: 0.5rem;

}

.search-form input{
  outline: none;
  padding: 0.5rem;
  background: transparent;
  color: #282c34;
  border: 0;
  font-size: 1rem;
  margin-left: 1rem;
  width: 30rem;
}

.search-form input::placeholder{
  color: #282c34;
  opacity: 0.6;
}


.search-form .logo-input{
  color: #282c34;
  padding: 0.6rem;
  cursor: pointer;
  
}

.search-form .logo-input:hover{
  animation: input-shake infinite 1s ease-in-out;
}

.menu-div{
  width: 60%;
  margin: auto;
}


.h4-trend{
  margin-top: 2.5rem;
  margin-bottom: 0.2rem;
  width: 100%;
  
}

 hr {
  border: white 0.05rem solid;
  margin-top: 12px;
  width: 100%;
}

ul{
  list-style: none;
  padding: 0.3rem 0 3rem 0;
  margin:0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: auto;
  
}

li{
  margin: 0.6rem;
  padding: 0.5rem;
  flex-grow: 1;
  max-width:max-content;
  min-width: 8rem;
}

li a{
  text-decoration: none;
  color: white;
  padding: 1rem;
  transition: all 0.2s ease;
  cursor: pointer;
}

a:hover{
  background-color: #484f5c;
  border-radius: 0.6rem;
}



.gif-grid {
  background-color: #282c34;
  min-height: 100vh;
  color: white;

/*   display:grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  gap: 0 10px;
  grid-template-rows: masonry; */
  column-count: 4;
  column-gap: 0.5em;
}

@media (max-width: 900px){
  
  .gif-grid{
    column-count: 3;
  }

  .menu-trend{
    display: none;
    width: 0%;
  }

  ul{
    list-style: none;
    padding: 0.3rem 0 3rem 0;
    margin:0;
    display: none;
    justify-content: center;
    align-items: center; 
    transition: all 0.3s ease;
  }

  .menu-div:hover ul {
    list-style: none;
    padding: 0.3rem 0 3rem 0;
    margin:0;
    display: flex;
    justify-content: center;
    align-items: center; 
  }
 
 
}
@media (max-width: 500px){
  
  .gif-grid{
    column-count: 2;
  }


}


.h3-grid {
 margin-left: 1.5rem;
 margin-bottom: 0.4rem;
}


.div-gif{
  height: auto;
  margin: 0; 
  -webkit-column-break-inside: avoid;
  page-break-inside: avoid;
  break-inside: avoid;
  display: inline-block;
  width: 100%;
 
}

.div-gif img{
  width: 100%;
  margin: 0;
}




@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes input-shake {
  0% {
    transform: rotate(0deg);
    transform: scale(1.2);
  }
  30% {
    transform: rotate(6deg);
  }
  40%{
    transform: rotate(0deg);
  }
  50%{
    transform: rotate(6deg);
  }
  60%{
    transform: rotate(0deg);
    transform: scale(1.2);
  }
  100% {
    transform: rotate(0deg);
  }
}

@media only screen and (max-width: 499px) {
  .search-form{
    display: flex;
    width: auto;
    margin: auto;
    background-color: white;
    border-radius: 0;
  }
  
}




